/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1.5;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
/* end eric meyer's reset */
.clear:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
.clear {
  display: inline-block;
}
/* hide IE mac \*/
* html .clear {
  height: 1%;
}
.clear {
  display: block;
}
/* */
* {
  box-sizing: border-box;
}
html,
body {
  font-size: 62.5%;
  height: 100%;
}
body {
  font-size: 1.6rem;
  font-family: "proxima-nova", "Verdana", "Helvetica", Arial, sans-serif;
  position: relative;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "proxima-nova", "Verdana", "Helvetica", Arial, sans-serif;
  line-height: 1;
  font-weight: bold;
}
@media screen and (min-width: 640px) {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    line-height: 1.2;
  }
}
h1 {
  font-size: 5.5rem;
}
img {
  height: auto;
  max-width: 100%;
}
.hidden {
  display: none;
}
.visually-hidden {
  color: transparent;
  font: 0/0;
  height: 0;
  overflow: hidden;
  width: 0;
}
.circle-spinner {
  animation-name: loader-spin;
  animation-duration: 2500ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  border: dashed 0.1rem #f1453b;
  border-radius: 50%;
  height: 2.2rem;
  margin: 2rem auto;
  width: 2.2rem;
}
.circle-spinner.large {
  width: 5rem;
  height: 5rem;
  border-width: 0.4rem;
}
@-webkit-keyframes loader-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes bladeFade {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.spinner {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}
.spinner .blade {
  position: absolute;
  left: 0;
  top: 50%;
  margin-top: -4%;
  height: 8%;
  width: 100%;
}
.spinner .blade > div {
  width: 25%;
  margin: 0 0 0 auto;
  height: 100%;
  background: #f1453b;
  border-radius: 1000px;
  opacity: 1;
  animation-duration: 2500ms;
  animation-name: bladeFade;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}
.spinner .blade.d02 {
  transform: rotate(-30deg);
}
.spinner .blade.d02 > div {
  animation-delay: -208.33333333ms;
}
.spinner .blade.d03 {
  transform: rotate(-60deg);
}
.spinner .blade.d03 > div {
  animation-delay: -416.66666667ms;
}
.spinner .blade.d04 {
  transform: rotate(-90deg);
}
.spinner .blade.d04 > div {
  animation-delay: -625ms;
}
.spinner .blade.d05 {
  transform: rotate(-120deg);
}
.spinner .blade.d05 > div {
  animation-delay: -833.33333333ms;
}
.spinner .blade.d06 {
  transform: rotate(-150deg);
}
.spinner .blade.d06 > div {
  animation-delay: -1041.66666667ms;
}
.spinner .blade.d07 {
  transform: rotate(-180deg);
}
.spinner .blade.d07 > div {
  animation-delay: -1250ms;
}
.spinner .blade.d08 {
  transform: rotate(-210deg);
}
.spinner .blade.d08 > div {
  animation-delay: -1458.33333333ms;
}
.spinner .blade.d09 {
  transform: rotate(-240deg);
}
.spinner .blade.d09 > div {
  animation-delay: -1666.66666667ms;
}
.spinner .blade.d10 {
  transform: rotate(-270deg);
}
.spinner .blade.d10 > div {
  animation-delay: -1875ms;
}
.spinner .blade.d11 {
  transform: rotate(-300deg);
}
.spinner .blade.d11 > div {
  animation-delay: -2083.33333333ms;
}
.spinner .blade.d12 {
  transform: rotate(-330deg);
}
.spinner .blade.d12 > div {
  animation-delay: -2291.66666667ms;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.fadeIn {
  animation-duration: 1000ms;
  animation-fill-mode: forwards;
  animation-name: fadeIn;
}
.copy {
  margin: 0 auto;
  max-width: 72ch;
}
.inline {
  display: inline;
  margin: 0;
  padding: 0;
}
.inline li {
  display: inline;
}
ul.piped li,
ol.piped li {
  margin: 0;
  padding: 0;
}
ul.piped li::before,
ol.piped li::before {
  content: '|\00a0';
  display: inline-block;
}
ul.piped li:first-child::before,
ol.piped li:first-child::before {
  content: '';
  display: none;
}
h1 {
  font-size: 30px;
}
@media screen and (min-width: 640px) {
  h1 {
    font-size: 40px;
  }
}
.footer-topper {
  background-image: url('/img/display/striped_background.png');
  color: #fff;
  justify-content: space-between;
  padding: 1.6rem 1.6rem 1.6rem 0.6rem;
}
@media screen and (min-width: 480px) {
  .footer-topper {
    display: flex;
  }
}
.footer-topper-links {
  font-weight: 100;
}
.footer-topper-links a {
  color: #fff;
  display: inline-block;
  padding: 0 1rem;
}
.footer-topper-links a:link,
.footer-topper-links a:visited {
  text-decoration: none;
}
.footer-topper-links a:hover,
.footer-topper-links a:active {
  text-decoration: underline;
}
.footer-topper-social {
  display: flex;
  justify-content: space-around;
  width: 14rem;
}
.footer-topper-social span {
  padding: 0.4rem;
}
.follow-us-icon {
  background-repeat: no-repeat;
  color: transparent;
  display: inline-block;
  font: 0/0;
  height: 3rem;
  margin-left: 0.5rem;
  overflow: hidden;
  width: 3rem;
}
.follow-us-icon.fb {
  background-image: url('/img/share/icon_facebook.png');
}
.follow-us-icon.tw {
  background-image: url('/img/share/icon_twitter.png');
}
.content {
  background: #fff;
  padding-top: 60px;
}
.content .wrap {
  margin: 0px auto;
  max-width: 1200px;
  text-align: center;
}
.card {
  display: inline-block;
  max-width: 474px;
  width: 100%;
  border: 1px solid #e5ebec;
  border-radius: 10px;
  padding: 15px;
  background: white;
  box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.1);
  margin: 0px 0px 25px 0px;
}
.card dt {
  display: inline-block;
  vertical-align: top;
}
.card dt img {
  max-width: 83px;
  height: auto;
  border-radius: 100px;
}
.card dd {
  display: inline-block;
  vertical-align: top;
  max-width: 320px;
  padding: 0px 0px 10px 0px;
}
.card dd p {
  padding-right: 0px;
  color: #5d676f;
  font-size: 15px;
  font-weight: 400;
  margin-bottom: 0px;
  text-align: center;
}
.card dd h3 {
  color: #4a5056;
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
  text-align: center;
}
.card dd h4 {
  color: #a3aeb7;
  font-size: 13px;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 20px;
}
@media screen and (min-width: 640px) {
  .card {
    margin: 0px 7px 25px 6px;
    height: 230px;
  }
  .card dt {
    margin-right: 25px;
  }
  .card dd p {
    text-align: left;
  }
  .card dd h3 {
    text-align: left;
  }
  .card h3 {
    text-align: left;
  }
  .card h4 {
    text-align: left;
  }
  .card dd {
    padding-top: 10px;
  }
}
/*
* Everything is relative.
*
* Including this component. To resize, overwrite the font-size of
* div.share, button.social somewhere in your code.
*
* *****************
* Don't touch this.
* *****************
*/
div.share,
button.social {
  font-size: 1.4rem;
}
div.share {
  border-radius: 7.142857143em;
  display: inline-block;
  text-align: center;
}
@media screen and (min-width: 480px) {
  div.share {
    padding: 1rem;
    background: rgba(0, 0, 0, 0.1);
  }
}
button.social {
  border: 0;
  border-radius: 7.142857143em;
  box-sizing: border-box;
  color: white;
  cursor: pointer;
  display: inline-block;
  font-family: "proxima-nova", "Verdana", "Helvetica", Arial, sans-serif;
  font-weight: bold;
  height: 2.142857143em;
  line-height: 2;
  margin-left: 0.357142857em;
  margin-right: 0.357142857em;
  padding: 0 1em 0 2.857142857em;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
}
button.social::before {
  content: '';
  display: block;
  left: 1em;
  position: absolute;
  top: 0.357142857em;
}
button.social.twitter {
  background: #17a1ce;
}
button.social.twitter::before {
  background-position: 0 0;
  width: 1.42857143em;
  height: 1.42857143em;
  background-image: url('../images/share-icons.png');
  background-repeat: no-repeat;
  background-size: auto 1.42857143em;
}
button.social.facebook {
  background: #3b5a95;
}
@media screen and (min-width: 480px) {
  button.social.facebook {
    margin-bottom: 0;
  }
}
button.social.facebook::before {
  background-position: -50% 0;
  width: 1.42857143em;
  height: 1.42857143em;
  background-image: url('../images/share-icons.png');
  background-repeat: no-repeat;
  background-size: auto 1.42857143em;
  background-position: 100% 0;
}
button.social.donate {
  padding-left: 1em;
  background: #e6b520;
}
.share-icon.twitter {
  background-position: 0 0;
  width: 6rem;
  height: 6rem;
  background-image: url('../images/share-icons.png');
  background-repeat: no-repeat;
  background-size: 120px 60px;
}
.share-icon.facebook {
  background-position: -50% 0;
  width: 6rem;
  height: 6rem;
  background-image: url('../images/share-icons.png');
  background-repeat: no-repeat;
  background-size: 120px 60px;
}
