header.main-site {
  background: #11436d;
  font-size: 0;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.2);
  position: fixed;
  width: 100%;
  z-index: 10;
}
header.main-site div {
  max-width: 1440px;
  width: 100%;
  margin: 0px auto;
  background: #1978c8;
  position: relative;
  min-height: 50px;
  text-align: right;
}
header.main-site div h1 {
  position: absolute;
  left: 0px;
  top: 0px;
  font-size: 0px;
  color: rgba(0, 0, 0, 0);
  height: 50px;
  width: 217px;
  background: #1978c8 url(../img/header/logo.png) right center no-repeat;
  background-size: 174px 30px;
  margin: 0px;
}
header.main-site div h1::before {
  content: " ";
  display: block;
  width: 43px;
  height: 50px;
  background: #1978c8 url(../img/header/heart.png) center center no-repeat;
  background-size: 26px 26px;
}
header.main-site div a.donate {
  display: none;
}
header.main-site div hr {
  display: none;
}
header.main-site div button.hamburgler {
  font-size: 0px;
  color: rgba(0, 0, 0, 0);
  display: inline-block;
  border: 0px;
  width: 40px;
  height: 40px;
  border-radius: 5px;
  background: transparent url(../img/header/hamburgler.png) center center no-repeat;
  margin: 5px 5px 5px 0px;
  background-size: 30px 30px;
  vertical-align: top;
  opacity: .5;
  cursor: pointer;
}
header.main-site div button.hamburgler:hover {
  opacity: 1;
}
header.main-site div button.hamburgler.focused {
  background-color: #11436d;
  opacity: 1;
}
header.main-site div nav {
  display: none;
  font-size: 19px;
}
header.main-site div nav ul {
  padding: 10px;
  background: #15558b;
}
header.main-site div nav ul li {
  text-align: left;
  font-family: "myriad-pro", Impact;
  font-weight: 600;
  text-transform: uppercase;
  padding: 5px 0px;
}
header.main-site div nav ul li a {
  color: white;
  text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.1);
}
header.main-site div nav.visible {
  display: block;
}
header.main-site div button.share {
  display: none;
}
@media screen and (min-width: 480px) {
  header.main-site div {
    min-height: 65px;
  }
  header.main-site div h1 {
    width: 270px;
    height: 65px;
    background: #1978c8 url(../img/header/logo.png) right center no-repeat;
    background-size: 220px 38px;
  }
  header.main-site div h1::before {
    height: 65px;
    width: 50px;
    background-size: 30px 30px;
  }
  header.main-site div button.hamburgler {
    width: 40px;
    height: 40px;
    margin: 12px 10px 13px 0px;
  }
}
@media screen and (min-width: 768px) {
  header.main-site div h1 {
    width: 293px;
  }
  header.main-site div h1::before {
    width: 55px;
    background-color: #11436d;
  }
  header.main-site div button.hamburgler {
    display: none;
  }
  header.main-site div nav {
    display: inline-block;
    vertical-align: top;
    font-size: 15.5px;
  }
  header.main-site div nav ul {
    padding: 0px 15px 0px 0px;
    background: #1978c8;
  }
  header.main-site div nav ul li {
    display: inline-block;
    line-height: 65px;
    padding: 0px;
  }
  header.main-site div nav ul li a {
    text-decoration: none;
  }
  header.main-site div nav ul li a:hover {
    text-decoration: underline;
  }
  header.main-site div nav ul li::before {
    color: #1664b3;
    content: '|\00a0';
    display: inline-block;
    padding: 0px 8px 0px 10px;
  }
  header.main-site div nav ul li:first-child::before {
    content: '';
    display: none;
  }
  header.main-site div nav.visible {
    display: inline-block;
  }
}
@media screen and (min-width: 960px) {
  header.main-site div h1 {
    width: 303px;
  }
  header.main-site div h1::before {
    width: 65px;
    background-color: #11436d;
  }
  header.main-site div a.donate {
    display: inline-block;
    width: 164px;
    height: 65px;
    font-family: "myriad-pro", Impact;
    font-size: 21px;
    font-weight: 600;
    line-height: 65px;
    text-align: center;
    color: white;
    text-decoration: none;
    text-transform: uppercase;
    background: #166bb3;
    text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.1);
    letter-spacing: 1px;
  }
  header.main-site div a.donate:hover {
    background: #855773;
  }
}
@media screen and (min-width: 1120px) {
  header.main-site div button.share {
    display: inline-block;
    height: 65px;
    border: 0px;
    cursor: pointer;
    font-size: 0px;
    color: transparent;
    vertical-align: top;
    background: #11436d;
  }
  header.main-site div button.share.facebook {
    background: #11436d url(../img/header/facebook.png) 15px 18px no-repeat;
    background-size: 30px 30px;
    width: 50px;
  }
  header.main-site div button.share.twitter {
    background: #11436d url(../img/header/twitter.png) 5px 18px no-repeat;
    background-size: 30px 30px;
    width: 50px;
  }
  header.main-site div button.share:hover {
    background-color: black;
  }
}
footer.main-site {
  margin-bottom: 0;
  background: #1978c8;
}
footer.main-site .gutter {
  position: relative;
  padding: 25px 10px 30px 10px;
  max-width: 960px;
  width: 100%;
  margin: 0 auto;
}
footer.main-site .gutter img {
  width: 357px;
  max-width: 100%;
  margin-top: 15px;
  margin-bottom: 10px;
  height: auto;
}
footer.main-site .gutter div.sharea {
  margin-bottom: 10px;
}
footer.main-site .gutter div.sharea span {
  display: inline;
  line-height: 30px;
  vertical-align: top;
  color: white;
  text-transform: uppercase;
  font-family: "proxima-nova", "Verdana", "Helvetica", Arial, sans-serif;
  font-weight: 600;
  font-size: 15px;
  margin-right: 5px;
}
footer.main-site .gutter div.sharea button {
  border: 0;
  font-size: 0;
  color: transparent;
  width: 30px;
  height: 30px;
  cursor: pointer;
  margin-left: 5px;
  vertical-align: top;
}
footer.main-site .gutter div.sharea button.facebook {
  background: url(/img/header/facebook.png);
  background-size: 30px 30px;
}
footer.main-site .gutter div.sharea button.twitter {
  background: url(/img/header/twitter.png);
  background-size: 30px 30px;
}
footer.main-site .gutter hr {
  border: 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}
footer.main-site .gutter nav {
  padding-top: 20px;
}
footer.main-site .gutter nav ul {
  max-width: 800px;
}
footer.main-site .gutter nav ul li {
  width: 180px;
  font-size: 14px;
  display: inline-block;
  padding-bottom: 5px;
}
footer.main-site .gutter nav ul li a {
  color: white;
  font-weight: 400;
  text-decoration: none;
}
footer.main-site .gutter nav ul li a strong {
  font-weight: 600;
  text-decoration: underline;
}
footer.main-site .gutter nav ul li a:hover {
  text-decoration: underline;
}
@media screen and (min-width: 640px) {
  footer.main-site .gutter div.sharea {
    position: absolute;
    right: 10px;
    top: 60px;
